:root {
  --redhatdisplay: "Red Hat Display", sans-serif;
}

:root {
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --verypale: hsl(225, 100%, 98%);
  --desaturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(223, 47%, 23%);
  --light: #fff;
  --dark: #000;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--redhatdisplay);
  background: var(--paleblue) url(/images/pattern-background-desktop.svg);
  min-height: 100vh;
}
body .main {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
}
body .main .attribution {
  margin-top: rem(30);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  position: relative;
  height: 35.625rem;
  width: 20.625rem;
  border-radius: 1.25rem;
  margin: 5rem 0 0;
}
.card__banner, .card__banner img {
  height: 10rem;
  width: 20.5rem;
  border-radius: 1.25rem 1.25rem 0 0;
}

.summary {
  height: 25.4375rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background: var(--light);
  border-radius: 0 0 1.25rem 1.25rem;
}
.summary__title {
  margin: 1.875rem 0 0;
  font-size: 1.25rem;
  font-weight: 900;
  color: var(--darkblue);
}
.summary__description {
  margin: 1.5625rem 0;
  text-align: center;
  line-height: 1.5625rem;
  padding: 0 1.875rem;
  color: var(--desaturatedblue);
}
.summary .plan {
  background: var(--verypale);
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5rem;
  width: 17.5rem;
}
.summary .plan__icon {
  padding: 0.9375rem;
}
.summary .plan__block {
  display: flex;
  flex-direction: column;
}
.summary .plan__block__name {
  margin: 0 0 0.3125rem 0;
  font-weight: 900;
  color: var(--darkblue);
}
.summary .plan__block__price {
  margin: 0 0 0.3125rem 0;
  color: var(--desaturatedblue);
}
.summary .plan__cta {
  padding: 0.9375rem 0.9375rem 0.9375rem 2.1875rem;
  color: var(--brightblue);
  font-weight: 700;
}
.summary .plan__cta:hover {
  opacity: 0.7;
}
.summary .payment__cta {
  background: var(--brightblue);
  height: 2.5rem;
  width: 17.5rem;
  margin: 1.5625rem 0 0;
  text-align: center;
  align-items: center;
  border-radius: 0.625rem;
}
.summary .payment__cta:hover {
  opacity: 0.7;
}
.summary .payment__cta__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: var(--light);
}
.summary .cancel_cta {
  margin: 1.25rem 0 0;
  color: var(--desaturatedblue);
}